.experience_container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}

.experience_container > div {
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem ;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience_container > div:hover{
    background: transparent;
    border-color: var(--color-primart-variant);
    cursor: default;
}

.experience_container > div h3{
    text-align: center;
    color: var(--color-primary);
}

.experience_content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}

.experience_details{
    display: flex;
    gap: 1rem;
}

.experience_details-icon{
    margin-top: 6px;
    color: var(--color-primary);
    font-size: 20px;
}
/* to arrange size of web dev specialization course */
.icon{
    font-size: 60px;
}
/* to change size of icon of related courses */
.key{
    font-size:30px;
}

/* ------ Media query for medium devices --------  */

@media screen and (max-width:1024px) {
    .experience_container{
        grid-template-columns: 1fr;
    }
    .experience_container > div{
        width: 80%;
        padding: 2rem ;
        margin: 0 auto;
    }
    .experience_content{
        padding: 1rem;
    }
}

/* ------ Media query for small devices --------  */

@media screen and (max-width:600px) {
    .experience_container{
        gap: 1rem;
    }
    .experience_container > div{
        width: 100%;
        padding: 2rem 1rem;
    }
}